import axios from 'axios'
import qs from 'qs'
var studyController = {
  bd_access_token: '24.68ccaa3a3b7104e43ba80813b945f30b.2592000.1572178167.282335-17360044',
  audio: null,
  isPlaying: false,
  /**
   * 播放合成语音
   * @param {Object} text 代播放的文字内容
   */
  playAudio: function(text, callback) {
    let self = this
    let src = 'https://tsn.baidu.com/text2audio?lan=zh&cuid=123456&ctp=1&tok=' + self.bd_access_token +
      '&tex=' + text
    this.audio = new Audio(src)
    this.audio.onended = function() {
      callback()
    }
    this.audio.play()
    this.isPlaying = true
  },
  stop: function() {
    if (this.audio && this.isPlaying) {
      this.audio.pause();
      this.audio.currentTime = 0;
    }
  }

}

export default studyController



