<template>
  <div v-if="loadVisible" class="mask">
    <div class="v-loading">
      <countDown :fire="fire" :time="timer" :tiping="tiping" @onEnd="onEnd" :statusChange="statusChange"
        @onStatusChange="onStatusChange"></countDown>
    </div>
  </div>
</template>

<script>
import countDown from 'vue-canvas-countdown'
export default {
  name: 'vloading',
  components: {
    countDown
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    time: {
      type: Number,
      default: 30
    }
  },
  data () {
    return {
      loadVisible: this.value,
      statusChange: [4000, 1500],
      timer: this.time,
      fire: 0,
      tiping: {
        text: '倒计时进行中',
        color: '#fff'
      },
      tipend: {
        text: '倒计时结束',
        color: '#fff'
      }
    }
  },
  mounted () {
    this.onStart()
  },
  watch: {
    vlaue (newValue, oldValue) {
      this.loadVisible = this.value
      this.onStart()
    },
    time (newValue, oldValue) {
      this.timer = this.time
      this.onStart()
    }
  },
  methods: {
    onStart () {
      this.loadVisible = this.value
      this.timer = this.time
      // 配置参数（更多配置如下表）
      this.tiping = {
        text: 'speak',
        color: '#fff'
      }
      this.tipend = {
        text: '',
        color: '#fff'
      }
      // 启动倒计时(效果如上图所示)
      this.fire++
    },
    onEnd () {
      this.loadVisible = false
      this.$emit('input', this.loadVisible)
      this.$emit('end')
      // console.log(new Date(), 'end')
    },
    onStatusChange (payload) {
      // console.log(new Date(), payload)
      if (payload == 3) {
        this.$emit('leftone')
      }
    }
  }
}
</script>

<style scoped>
  .mask {
    position: fixed;
    width: 100%;
    height: 100%;
    opacity: 0.8;
    background: #000000;
    top: 0;
    left: 0;
    text-align: center;
    z-index: 11111;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    flex-flow: nowrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    overflow: hidden;
  }

  .mask .v-loading {
    flex: 1;
  }

  .v-loading {
    font-size: 60px;
  }

  .el-loading-spinner {
    font-size: 80px;
  }
</style>



