<template>
  <div class="camera">
    <!-- <canvas ref="canvas" hidden width="100%"></canvas> -->
    <video ref="video" width="100%" height="100%" autoplay></video>
  </div>
</template>

<script>
import StudyController from '@/api/StudyController.js'
export default {
  name: 'camera',
  data () {
    return {
      recorder: null
    }
  },
  mounted () {
    this.callCamera()
  },
  beforeDestroy () {
    this.closeCamera()
  },
  methods: {
    // 调用摄像头
    callCamera: function () {
      // H5调用电脑摄像头API
      navigator.mediaDevices.getUserMedia({
        video: true,
        audio: false
      }).then(success => {
        // 摄像头开启成功
        this.$refs['video'].srcObject = success
        // 实时拍照效果
        this.$refs['video'].play()
      }).catch(() => {
        console.error('摄像头开启失败，请检查摄像头是否可用！')
      })
    },
    // 拍照
    photograph: function () {
      var ctx = this.$refs['canvas'].getContext('2d')
      // 把当前视频帧内容渲染到canvas上
      ctx.drawImage(this.$refs['video'], 0, 0, 640, 480)
      // 转base64格式、图片格式转换、图片质量压缩
      var imgBase64 = this.$refs['canvas'].toDataURL('image/jpeg', 0.7)
      // console.log(imgBase64)
      // 由字节转换为KB 判断大小
      var str = imgBase64.replace('data:image/jpeg;base64,', '')
      // console.log(str)
      // var strLength = str.length
      // var fileLength = parseInt(strLength - (strLength / 8) * 2)
      // // 图片尺寸  用于判断
      // var size = (fileLength / 1024).toFixed(2)
      // console.log(new Date(), size)
      StudyController.chkSture(str, function (data) {
        console.log(data.classname)
      })
    },
    // 关闭摄像头
    closeCamera: function () {
      if (!this.$refs['video'].srcObject) return
      let stream = this.$refs['video'].srcObject
      let tracks = stream.getTracks()
      tracks.forEach(track => {
        track.stop()
      })
      this.$refs['video'].srcObject = null
    }
  }
}
</script>

<style lang="scss" scoped>
  .camera {
    width: 100%;
    height: 100%;
  }
</style>



